<template>
	<v-text-field
		v-model.trim="username"
		filled
		dark
		dense
		outlined
		required
		label="Login"
		:error-messages="errorMessage"
		:error="error"
		@input="updateValue"
	/>
</template>

<script>
export default {
	props: {
		disable: {
			type: Boolean,
			default: false
		},
		error: {
			type: Boolean,
			default: false
		},
		errorMessage: {
			type: String,
			default: ''
		},
		value: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			username: this.value
		};
	},
	watch: {
		value(newVal) {
			this.username = newVal;
		},
	},
	methods: {
		updateValue(newVal) {
			this.username = newVal;
			this.$emit('update:value', this.username);
		}
	}
};
</script>