<template>
	<div class="wrapper">
		<div>
			<v-checkbox
				v-model="checked" 
				dark
				hide-details
				@change="updateValue"
			/>
		</div>
		<span class="checkbox_label">Remember me</span>
	</div>
</template>

<script>
export default {
	props: {
		value: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			checked: this.value
		};
	},
	watch: {
		value(newVal) {
			this.checked = newVal;
		}
	},
	methods: {
		updateValue () {
			this.$emit('input', this.checked);
		}
	}
};
</script>

<style lang="scss" scoped>
	.wrapper {
		display: flex;
		align-items: center;
		padding: 0;
	}
	.checkbox_label {
		margin-left: 7px;

		color: rgba(255, 255, 255, 0.707);
		font-size: 12px;
	}
	.wrapper::v-deep .v-input {
		transform: scale(0.8);
		margin: 0;
		.v-input--selection-controls__input {
			margin: 0;
		}
	}
</style>