<template>
	<div id="login_screen">
		<vue-element-loading 
			:active="loading"
			:background-color="'rgba(255, 255, 255, .2)'"
			:is-full-screen="true"
			:color="'blue'"
		/>
		<div class="login_screen__container">
			<div class="login_screen__logo">
				<img 
					class="login_screen__logo_img"
					src="@/assets/home/logo.png"
				>
			</div>
			<div class="login_screen__form">
				<login-input
					:value="inputData.username.value"
					:error="inputData.username.error"
					:error-message="inputData.username.errorMessage"
					@update:value="inputData.username.value = $event"
				/>
				<password-input
					:error="inputData.password.error"
					:error-message="inputData.password.errorMessage"
					@update:value="inputData.password.value = $event"
				/>
				<remember-me
					v-model="rememberMe"
				/>
				<div class="login_screen__buttons">
					<v-btn
						class="login_screen__buttons_submit"
						elevation="5"
						@click="submit"
					>
						Login
					</v-btn>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import LoginInput from '@/components/InputLogin.vue';
import PasswordInput from '@/components/InputPassword.vue';
import RememberMe from '@/components/RememberMe.vue';

import { getCookie, setCookie } from '@/helpers/cookies.js';
import checkCredentials from '@/helpers/checkCredentials.js';

import VueElementLoading from 'vue-element-loading';

export default { 
	name: 'Login',
	components: {
		VueElementLoading,
		LoginInput,
		PasswordInput,
		RememberMe,
	},
	data() {
		return {
			inputData: {
				username: {
					value: '',
					error: false,
					errorMessage: ''
				},
				password: {
					value: '',
					error: false,
					errorMessage: '',
				},
			},
			rememberMe: false,
			loading: false,
			
		};
	},
	created() {
		const rememberMe = localStorage.getItem('remember_me');
		const isLoggedIn = getCookie('user_state');
		if(rememberMe) this.rememberMe = JSON.parse(rememberMe);
		if(isLoggedIn) this.$router.push({ name: 'Home' }).catch(() => {});
	},
	mounted() {
		if(this.rememberMe) {
			const userData = getCookie('user_data');
			if(userData) this.inputData.username.value = JSON.parse(userData).username || '';
		}
	},
	methods: {
		async submit() {
			localStorage.setItem('remember_me', this.rememberMe);
			this.showErrorAlert = !this.showErrorAlert;
			if(this.validate()) {
				this.loading = true;
				try {
					const login = await checkCredentials(this.inputData.username.value, this.inputData.password.value);

					const date = new Date();
					date.setDate(date.getDate() + 7);
					const expires = date.toUTCString();

					if(login.ResponseCode === 200) {
						setCookie('_token', login.Data, { expires: expires });
						setCookie('user_state', 'logged_in', { expires: expires });
						if(this.rememberMe) {
							setCookie('user_data', JSON.stringify({ username: this.inputData.username.value }), { expires: expires });
						}
						this.$router.push({ name: 'Home', query: { } }).catch(() => {});
					} else {
						this.$toast.error(login.ResponseMessage, {
							timeout: 2000
						});
					}
				} catch {
					this.loading = false;
					this.$toast.error('Сервер не отвечает. Попробуйте позже.', {
						timeout: 2000
					});
				}
				this.loading = false;
			}
		},
		validate() {
			let valid = true;
			Object.values(this.inputData).forEach(item => {
				if(!item.value) {
					item.error = true;
					item.errorMessage = '*Обязательное поле';
					valid = false;
				} else {
					item.error = false;
					item.errorMessage = '';
				}
			});
			return valid;
		}
	}
};
</script>

<style lang="scss" scoped>
	#login_screen {
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		justify-content: center;

		width: 100%;
		height: 100vh;
		background-image: url('@/assets/home/bg.png');
		background-size: cover;
	}
	.login_screen {
		&__container {
			width: 50%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			height: 100%;
			margin: 0 auto;
		}
		@media (max-width: 1024px) {
			&__container {
				width: 60%;
			}
		}
		@media (max-width: 768px) {
			&__container {
				width: 90%;
			}
		}
		&__logo {
			display: flex;
			justify-content: center;
			margin-bottom: 5rem;
			&_img {
				width: 136px;
				height: 92px;
			}
		}
		&__buttons {
			margin-top: 3rem;
			display: flex;
			justify-content: center;
			&_submit {
				width: 50%;
			}
		}
	}
	.btn_size {
		width: 150px
	}
</style>