export default async (username, password) => {
	try {
		const myHeaders = new Headers();
		myHeaders.append('Content-Type', 'application/json');

		const raw = JSON.stringify({
			'username': username,
			'password': password
		});

		const requestOptions = {
			method: 'POST',
			headers: myHeaders,
			body: raw,
			redirect: 'follow'
		};

		const response = await fetch('https://testkit.kz/user/login', requestOptions);
		const result = await response.json();
		return result;
	} catch (error) {
		return null;
	}
};
