<template>
	<v-text-field
		v-model.trim="password"
		dark
		filled
		dense
		outlined
		required
		:disabled="disable"
		:error-messages="errorMessage"
		:error="error"
		label="Password"
		:append-icon="showPassword ? 'fa-thin fa-eye' : 'fa-sharp fa-light fa-eye-slash'"
		:type="showPassword ? 'text' : 'password'"
		class="password-input"
		@click:append="showPassword = !showPassword"
		@input="updateValue"
	/>
</template>

<script>
export default {
	props: {
		disable: {
			type: Boolean,
			default: false
		},
		errorMessage: {
			type: String,
			default: ''
		},
		error: {
			type: Boolean,
			default: false
		},
	},
	data() {
		return {
			password: '',
			showPassword: false
		};
	},
	methods: {
		updateValue(newVal) {
			this.password = newVal;
			this.$emit('update:value', this.password);
		}
	}
};
</script>

<style lang="scss" scoped>
	.password-input::v-deep {
		.v-icon {
			font-size: 16px;
		}
	}
</style>